export default "/* \r\n====================================================\r\nFirst Section CSS\r\n====================================================\r\n*/\r\n\r\n.section-one {\r\n    height: 100vh;\r\n    width: 100%;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    color: #ffffff;\r\n    background: linear-gradient(135deg, #1e1f33 15%, #354153 73%, #354153 85%);\r\n    /*background-image: url(\"http://murmurationfest.com/wp-content/themes/murmuration/images/home_murmuration.png\");*/\r\n    background-position: center;\r\n    background-repeat: no-repeat;\r\n    background-size: cover;\r\n}\r\n\r\n.hero {\r\n    width: 100%;\r\n}\r\n\r\n.hero-txt {\r\n    flex-grow: 1;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: flex-end;\r\n    padding-bottom: 2em;\r\n}\r\n\r\n.hero-txt img {\r\n    height: 300px;\r\n    width: auto;\r\n    margin-top: 1em;\r\n    \r\n}\r\n\r\n.hero-btn {\r\n    padding: 1em;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.hero-btn .register-btn {\r\n    cursor: pointer;\r\n    text-transform: uppercase;\r\n    border: 1px solid #ffffff;\r\n    padding: 1em 2em 0 2em;\r\n}\r\n\r\n.hero-scroll {\r\n    padding: 1em;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.scroll-txt {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.animated {\r\n    -webkit-animation-duration: .5s;\r\n    animation-duration: .5s;\r\n    -webkit-animation-fill-mode: both;\r\n    animation-fill-mode: both;\r\n    -webkit-animation-timing-function: linear;\r\n    animation-timing-function: linear;\r\n    animation-iteration-count: infinite;\r\n    -webkit-animation-iteration-count: infinite;\r\n}\r\n\r\n@-webkit-keyframes bounce {\r\n    0%, 100% {\r\n        -webkit-transform: translateY(0);\r\n    }\r\n    50% {\r\n        -webkit-transform: translateY(-5px);\r\n    }\r\n}\r\n\r\n@keyframes bounce {\r\n    0%, 100% {\r\n        transform: translateY(0);\r\n    }\r\n    50% {\r\n        transform: translateY(-5px);\r\n    }\r\n}\r\n\r\n.bounce {\r\n    -webkit-animation-name: bounce;\r\n    animation-name: bounce;\r\n}\r\n\r\n/* \r\n====================================================\r\nSecond Section CSS\r\n====================================================\r\n*/\r\n\r\n.section-two {\r\n    width: 100%;\r\n    height: 100vh;\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: center;\r\n    align-items: center;\r\n    background-image: url('https://www.toptal.com/designers/subtlepatterns/patterns/papyrus.png');\r\n}\r\n\r\n.intro-img {\r\n    width: 50%;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.intro-img img {\r\n    height: 80vh;\r\n    width: auto;\r\n}\r\n\r\n.intro-txt {\r\n    width: 50%;\r\n    padding-right: 5em;\r\n}\r\n\r\n.intro-txt h1 {\r\n    font-family: 'Open Sans Condensed', sans-serif;\r\n    color: #6FAFAB;\r\n    text-transform: uppercase;\r\n    font-weight: lighter;\r\n}\r\n\r\n.intro-btn {\r\n    width: -webkit-fit-content;\r\n    width: -moz-fit-content;\r\n    width: fit-content;\r\n    height: -webkit-fit-content;\r\n    height: -moz-fit-content;\r\n    height: fit-content;\r\n    text-transform: uppercase;\r\n    cursor: pointer;\r\n    border: 1px solid #FA3A61;\r\n    color: #FA3A61;\r\n    letter-spacing: 1px;\r\n    padding: 0.7em 2em;\r\n}\r\n\r\n/* \r\n====================================================\r\nEvents Section CSS\r\n====================================================\r\n*/\r\n\r\n.section-three{\r\n    height: 100vh;\r\n    width: 100%;\r\n    background: linear-gradient(135deg, #1e1f33 15%, #354153 73%, #354153 85%);\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\n.events-title{\r\n    height: 15vh;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.events-title h1{\r\n    text-transform: uppercase;\r\n    color: #6FAFAB;\r\n    text-align: center;\r\n}\r\n\r\n.events-wrapper{    \r\n    height: 75vh;\r\n    display: flex;\r\n    flex-direction: row;\r\n}\r\n\r\n.events-wrapper .event{    \r\n    height: 100%;\r\n    width: 25%;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    text-align: center;\r\n    color: #6FAFAB;\r\n}\r\n\r\n.events-wrapper .event .circle{\r\n    height: 200px;\r\n    width: 200px;\r\n    border-radius: 50%;\r\n    background-color: #EAFFF8;\r\n}\r\n\r\n.event-btn-container{\r\n    height: 10vh;\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n/* \r\n====================================================\r\nFourth Section CSS App Advert\r\n====================================================\r\n*/\r\n\r\n.section-four {\r\n    height: 100vh;\r\n    width: 100%;\r\n    background-color: #EAFFF8;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.ad-img{\r\n    width: 50%;\r\n    height: 100vh;    \r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.ad-img img{\r\n    width: auto;\r\n    height: 80%;\r\n}\r\n\r\n.ad-text{\r\n    width: 50%;\r\n}\r\n\r\n.ad-text h1, .ad-text h2{\r\n    font-weight: lighter;    \r\n    color: #6FAFAB;\r\n}\r\n\r\n.ad-text a img{\r\n    width: 40%;\r\n}\r\n\r\n/*\r\n.dashed-ring{\r\n    height: 222px;\r\n    width: 222px;\r\n}\r\n.dashed-ring circle {\r\n    stroke: #80B8B4;\r\n    stroke-dasharray: 2 9;\r\n    -webkit-animation: 100s linear infinite;\r\n    animation: 100s linear infinite;\r\n    -webkit-transform-origin: center;\r\n    transform-origin: center;\r\n    -webkit-animation-name: u57800da0;\r\n    animation-name: u57800da0;\r\n}\r\n\r\n@keyframes u57800da0 {\r\n    0% {\r\n        -webkit-transform: rotate(0deg);\r\n        transform: rotate(0deg);\r\n    }\r\n    100% {\r\n        -webkit-transform: rotate(1turn);\r\n        transform: rotate(1turn);\r\n    }\r\n}*/\r\n\r\n/* \r\n====================================================\r\nFooter ECF6F7\r\n====================================================\r\n*/\r\n\r\n.footer-main{\r\n    width: 100%;\r\n    background-color: #4C4C4C;\r\n    display: flex;    \r\n    padding: 3em 0;\r\n}\r\n\r\n.footer-main h2{\r\n    color: #FA3A61;\r\n    text-transform: uppercase;\r\n    letter-spacing: 1px;\r\n}\r\n\r\n.footer-main p{\r\n    color: #ECF6F7;\r\n    text-transform: uppercase;    \r\n}\r\n\r\n.footer-logo{\r\n    width: 25%;\r\n}\r\n\r\n.footer-logo img{\r\n    height: auto;\r\n    width: 100%;\r\n    \r\n}\r\n\r\n.footer-links{\r\n    width: 20%;\r\n}\r\n\r\n.footer-social{\r\n    width: 30%;\r\n}\r\n\r\n.footer-sponsor{\r\n    width: 25%;\r\n}\r\n\r\n.footer-sponsor img{\r\n    height: auto;\r\n    width: 100%;    \r\n}\r\n\r\n.bottom-footer{\r\n    color : #ECF6F7;\r\n    text-align: center;\r\n    background-color: #212121;\r\n    padding: 1em 0;\r\n}\r\n\r\n/* \r\n====================================================\r\nMedia Queries And General Animation Stuff\r\n====================================================\r\n*/\r\n\r\n/*Open Nav Button*/\r\n\r\n.open-nav{\r\n    position: fixed;\r\n    top: 0;\r\n    right: 0;\r\n    margin: 1em 3em 0 0;\r\n    color: #FA3A61;\r\n    display: flex;\r\n    justify-content: space-evenly;\r\n    align-items: flex-end;\r\n    cursor: pointer;\r\n    padding: 1em;\r\n}\r\n\r\n.open-nav:hover{\r\n    background-color: #212c3d;\r\n}\r\n\r\n.open-nav img{\r\n    width: 42px;\r\n    height: auto;\r\n}\r\n\r\n.open-nav .nav-text{\r\n    font-weight: bold;\r\n    margin-left: 0.7em;\r\n    text-transform: uppercase;\r\n}\r\n\r\n/* Desktops */\r\n\r\n@media all and (min-width: 1024px) and (max-width: 1280px) {\r\n    .hero-txt {\r\n        flex-grow: 1;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: flex-end;\r\n        padding-bottom: 2em;\r\n    }\r\n    .hero-txt img {\r\n        height: 300px;\r\n        width: auto;\r\n        margin-top: 1em;\r\n    }\r\n}\r\n\r\n@media all and (min-width: 768px) and (max-width: 1024px) {\r\n    .hero-txt {\r\n        flex-grow: 1;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n    }\r\n    .hero-txt img {\r\n        height: 300px;\r\n        width: auto;\r\n    }\r\n}\r\n\r\n@media all and (min-width: 480px) and (max-width: 768px) {\r\n    .hero-txt {\r\n        flex-grow: 1;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n    }\r\n    .hero-txt img {\r\n        height: 300px;\r\n        width: auto;\r\n    }\r\n}\r\n\r\n@media all and (max-width: 480px) {\r\n    .hero-txt {\r\n        flex-grow: 1;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n        padding-top: 5em;\r\n    }\r\n    .hero-txt img {\r\n        height: 100px;\r\n        width: auto;\r\n    }\r\n}\r\n\r\n.glitch {\r\n    position: relative;\r\n    color: white;\r\n    letter-spacing: 2px;\r\n    /* Animation provies a slight random skew. Check bottom of doc for more information on how to random skew. */\r\n    animation: glitch-skew 1s infinite linear alternate-reverse;\r\n}\r\n\r\n.glitch::before {\r\n    content: attr(data-text);\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    left: 2px;\r\n    text-shadow: -2px 0 #ff00c1;\r\n    /* Creates an initial clip for our glitch. This works in a typical top,right,bottom,left fashion and creates a mask to only show a certain part of the glitch at a time. */\r\n    clip: rect(44px, 450px, 56px, 0);\r\n    /* Runs our glitch-anim defined below to run in a 5s loop, infinitely, with an alternating animation to keep things fresh. */\r\n    animation: glitch-anim 5s infinite linear alternate-reverse;\r\n}\r\n\r\n.glitch::after {\r\n    content: attr(data-text);\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    left: -2px;\r\n    text-shadow: -2px 0 #00fff9, 2px 2px #ff00c1;\r\n    animation: glitch-anim2 1s infinite linear alternate-reverse;\r\n}\r\n\r\n/* Creates an animation with 20 steaps. For each step, it calculates a percentage for the specific step. It then generates a random clip box to be used for the random glitch effect. Also adds a very subtle skew to change the 'thickness' of the glitch.*/\r\n\r\n@-webkit-keyframes glitch-anim {\r\n    0% {\r\n        clip: rect(62px, 9999px, 77px, 0);\r\n        transform: skew(0.23deg);\r\n    }\r\n    5% {\r\n        clip: rect(22px, 9999px, 2px, 0);\r\n        transform: skew(0.08deg);\r\n    }\r\n    10% {\r\n        clip: rect(87px, 9999px, 83px, 0);\r\n        transform: skew(0.69deg);\r\n    }\r\n    15% {\r\n        clip: rect(17px, 9999px, 78px, 0);\r\n        transform: skew(0.62deg);\r\n    }\r\n    20% {\r\n        clip: rect(97px, 9999px, 16px, 0);\r\n        transform: skew(0.1deg);\r\n    }\r\n    25% {\r\n        clip: rect(53px, 9999px, 84px, 0);\r\n        transform: skew(0.5deg);\r\n    }\r\n    30% {\r\n        clip: rect(18px, 9999px, 92px, 0);\r\n        transform: skew(0.24deg);\r\n    }\r\n    35% {\r\n        clip: rect(61px, 9999px, 28px, 0);\r\n        transform: skew(0.9deg);\r\n    }\r\n    40% {\r\n        clip: rect(73px, 9999px, 49px, 0);\r\n        transform: skew(0.09deg);\r\n    }\r\n    45% {\r\n        clip: rect(36px, 9999px, 43px, 0);\r\n        transform: skew(0.98deg);\r\n    }\r\n    50% {\r\n        clip: rect(7px, 9999px, 10px, 0);\r\n        transform: skew(0.48deg);\r\n    }\r\n    55% {\r\n        clip: rect(6px, 9999px, 100px, 0);\r\n        transform: skew(0.34deg);\r\n    }\r\n    60% {\r\n        clip: rect(37px, 9999px, 47px, 0);\r\n        transform: skew(0.47deg);\r\n    }\r\n    65% {\r\n        clip: rect(72px, 9999px, 12px, 0);\r\n        transform: skew(0.89deg);\r\n    }\r\n    70% {\r\n        clip: rect(94px, 9999px, 97px, 0);\r\n        transform: skew(0.59deg);\r\n    }\r\n    75% {\r\n        clip: rect(11px, 9999px, 9px, 0);\r\n        transform: skew(0.53deg);\r\n    }\r\n    80% {\r\n        clip: rect(12px, 9999px, 3px, 0);\r\n        transform: skew(0.61deg);\r\n    }\r\n    85% {\r\n        clip: rect(29px, 9999px, 94px, 0);\r\n        transform: skew(0.92deg);\r\n    }\r\n    90% {\r\n        clip: rect(54px, 9999px, 65px, 0);\r\n        transform: skew(0.62deg);\r\n    }\r\n    95% {\r\n        clip: rect(2px, 9999px, 3px, 0);\r\n        transform: skew(0.27deg);\r\n    }\r\n    100% {\r\n        clip: rect(5px, 9999px, 88px, 0);\r\n        transform: skew(0.23deg);\r\n    }\r\n}\r\n\r\n@keyframes glitch-anim {\r\n    0% {\r\n        clip: rect(62px, 9999px, 77px, 0);\r\n        transform: skew(0.23deg);\r\n    }\r\n    5% {\r\n        clip: rect(22px, 9999px, 2px, 0);\r\n        transform: skew(0.08deg);\r\n    }\r\n    10% {\r\n        clip: rect(87px, 9999px, 83px, 0);\r\n        transform: skew(0.69deg);\r\n    }\r\n    15% {\r\n        clip: rect(17px, 9999px, 78px, 0);\r\n        transform: skew(0.62deg);\r\n    }\r\n    20% {\r\n        clip: rect(97px, 9999px, 16px, 0);\r\n        transform: skew(0.1deg);\r\n    }\r\n    25% {\r\n        clip: rect(53px, 9999px, 84px, 0);\r\n        transform: skew(0.5deg);\r\n    }\r\n    30% {\r\n        clip: rect(18px, 9999px, 92px, 0);\r\n        transform: skew(0.24deg);\r\n    }\r\n    35% {\r\n        clip: rect(61px, 9999px, 28px, 0);\r\n        transform: skew(0.9deg);\r\n    }\r\n    40% {\r\n        clip: rect(73px, 9999px, 49px, 0);\r\n        transform: skew(0.09deg);\r\n    }\r\n    45% {\r\n        clip: rect(36px, 9999px, 43px, 0)